<template>
    <div class="catalog-cart-item">
        <div class="catalog-cart-item__wrap">
            <img class="catalog-cart-item__image" :src="require('../../assets/images/' + cart_item_data.image)" alt="img">
        </div>
        <div class="catalog-cart-item__info">
            <p>{{ cart_item_data.name }}</p>
            <p>{{ cart_item_data.price }} UAH</p>
            <p>{{ cart_item_data.article }}</p>
        </div>
        <div class="catalog-cart-item__quantity">
            <p>Qty:</p>
            <p class="catalog-cart-item__qty">
                <span class="catalog-cart-item__btn" @click="decrementItem">-</span>
                <span class="catalog-cart-item__number">{{ cart_item_data.quantity }}</span>
                <span class="catalog-cart-item__btn" @click="incrementItem">+</span>
            </p>
        </div>
        <button 
            @click="deleteFromCart"
            class="btn"
        >
            Delete
        </button>
    </div>
</template>

<script>
    export default {
        name: 'CatalogCartItem',
        components: {},  
        props: {
            cart_item_data: {
                type: Object,
                default() {
                    return {}
                }
            }
        },
        methods: {
            decrementItem() {
                this.$emit('decrementItem')
            },
            incrementItem() {
                this.$emit('incrementItem')
            },
            deleteFromCart() {
                this.$emit('deleteFromCart')
            }
        }
    }
</script>

<style lang="scss">
    .catalog-cart-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: nowrap;
        padding: $padding*2;
        margin-bottom: $margin*2;
        box-shadow: 0 0 8px 0 $color-shadow;
        @media (max-width: 920px) {            
            margin: 0 16px 16px;
        }
        @media (max-width: 600px) {
            flex-direction: column;
            margin: 0 50px 16px;
        }
        &__wrap {
            width: 100px;
            overflow: hidden;
        }
        &__image {
            height: 100%;
            width: 100%;
            object-fit: cover;
            object-position: center;
        }
        &__qty {
            display: flex;
            gap: 5px;
            @media (max-width: 600px) {
                margin-bottom: 30px;
            }
        }
        &__btn {
            cursor: pointer;
            color: $color-light;
            background-color: $main-color;
            padding: 3px 10px;
            border-radius: 50%;            
            display: flex;
            align-items: center;
            justify-content: center;
            &:hover {
                opacity: 0.6;
            }
        }
        &__number {
            border: 1px solid $color-border;
            padding: 4px 8px;
            width: 20px;
            text-align: center;
        }
    }
</style>
